.banner {
    color: white;
    object-fit: contain;
    height: 448px;
}

.banner__Content {
    margin-left: 30px;
    padding-top: 140px;
    height: 190;
}

.banner__title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner__description {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
}
.banner_button {
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
}
.banner_button:hover {
    color: #000;
    background-color: #fff;
    transition: all 0.2s;
}
.banner__fade {
    height: 7.1rem;

    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37, 37, 37, 0.6),
        #222222
    );
}
